import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
// import Login from '../pages/Login'
// import Register from '../pages/Register'
import ChangeInfo from '../pages/ChangeInfo'
import Release from '../pages/Release'
import Index from '../pages/Index'
import NotFound from '../pages/NotFound'
import UpdateArticle from '../pages/UpdateArticle'
import ArticleDetail from '../pages/DetailArticle'
import ArticleList from '../pages/ArticleList'
import Search from '../pages/Search'

export const Routers = () => (
    <Router>
        <Switch>
            <Route exact path="/" component={Index} />
            <Route path="/userInfo/:username" component={ChangeInfo} />
            <Route path="/publish" component={Release} />
            <Route path="/updateArticle/:username/:topic_id" component={UpdateArticle} />
            <Route exact path="/articleDetail/:username/:topic_id" component={ArticleDetail} />
            <Route exact path="/article/:username" component={ArticleList} />
            <Route exact path="/search" component={Search} />
            <Route path="*" component={NotFound} />
        </Switch>
    </Router>
);